@mixin font-face($family, $path, $name, $svg, $weight: normal, $style: normal) {
  @font-face {
    font-family: $family;
    src: url('#{$path}/#{$name}.eot');
    src: url('#{$path}/#{$name}.eot?#iefix') format('embedded-opentype'), url('#{$path}/#{$name}.woff') format('woff'), url('#{$path}/#{$name}.ttf') format('truetype'), url('#{$path}/#{$name}.svg##{$svg}') format('svg');
    font-weight: $weight;
    font-style: $style; } }

/* font-family: "Montserrat - Light"; */
@include font-face('Montserrat', '../fonts/Montserrat-Light', 'Montserrat-Light', 'Montserrat-Light', 300);

/* font-family: "Montserrat - Regular"; */
@include font-face('Montserrat', '../fonts/Montserrat-Regular', 'Montserrat-Regular', 'Montserrat-Regular');

/* font-family: "Montserrat - Medium"; */
@include font-face('Montserrat', '../fonts/Montserrat-Medium', 'Montserrat-Medium', 'Montserrat-Medium', 500);

/* font-family: "Montserrat - Bold"; */
@include font-face('Montserrat', '../fonts/Montserrat-Bold', 'Montserrat-Bold', 'Montserrat-Bold', 700);

/* font-family: "MyriadPro - Regular"; */
@include font-face('MyriadPro', '../fonts/MyriadPro-Regular', 'MyriadPro-Regular', 'MyriadPro-Regular');

/* font-family: "Velocity"; */
@include font-face('Velocity', '../fonts/Velocity', 'Velocity', 'Velocity');
